import Head from 'next/head';

import { PostData, loadBlogPosts } from '../loader';
import { PostCard } from '../components/PostCard';
import { generateRSS } from '../rssUtil';
import { Markdown } from '../components/Markdown';

const Home = (props: { readme: string; posts: PostData[] }) => {
  return (
    <div style={{ width: '100%', padding: '0px 10vw' }}>
      <Head>
        <title>Introducing Devii</title>
        <link rel="icon" href="/favicon.ico" />
      </Head>

      <div
        style={{
          padding: '50px 0px',
        }}
      >
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          {props.posts.map((post, j) => {
            return <PostCard post={post} key={j} />;
            // return (
            //   <a href={`/${post.path}`} target="_blank" key={j}>
            //     {post.title}
            //   </a>
            // );
            // return <PostCard post={post} key={j} />;
          })}
        </div>
      </div>

      <div
        style={{
          maxWidth: '800px',
          margin: 'auto',
        }}
      >
        <Markdown source={props.readme} />
      </div>
    </div>
  );
};
export default Home;

export const getStaticProps = async () => {
  const readmeFile = await import(`../${'README.md'}`);
  const readme = readmeFile.default;

  const posts = await loadBlogPosts();

  // comment out to turn off RSS generation
  // during build step.
  await generateRSS(posts);

  const props = { readme, posts };
  return { props };
};
